.row {
    display: flex;
    justify-content: center;
}
.square {
    width: 30px;
    height: 30px;
    border: 1px solid black;
    background-color: white;
    font-size: 15px;
}
.square0 {
    height: 30px;
}
h1 {
    text-align: center;
}
.buttonback {
    width: 130px;
    background-color: darkgreen;
    color: aliceblue;
    border-radius: 10px;
    height: 40px;
    font-size: 15px;
    font-weight: bold;
    margin-top: 20px;
}

.body{
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 0;
    width: 100%;
}
.ctrTable {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px; height: 450px;
    background-color: blueviolet;
    border-radius: 20px;
    margin-left: 25px;
    margin-top: 85px;
}
.winner-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.8); /* Nền mờ */
    display: flex;
    justify-content: center; /* Căn giữa theo chiều ngang */
    align-items: center;
    
  }
  .comment0{
    position: absolute;
    display: flex;
    justify-content: center; /* Căn giữa theo chiều ngang */
    width: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    top: 750px;
  }
  .comment {
    font-family: 'Pacifico', cursive;
    font-size: 60px; /* Kích thước chữ lớn */
    color: #FF007F; /* Màu hồng tươi */
    text-shadow: 
        
        0 0 20px #FF4500, /* Lớp đổ bóng bên ngoài có màu cam */
        0 0 30px #FFD700; /* Lớp đổ bóng ngoài cùng màu vàng */
    position: relative;
    text-align: center;
    
  }
  @keyframes sparkle {
    0% {
        text-shadow: 
            0 0 5px #FF69B4, 
            0 0 10px #FF69B4, 
            0 0 15px #FF69B4, 
            0 0 20px #FF4500, 
            0 0 30px #FFD700;
    }
    50% {
        text-shadow: 
            0 0 10px #FFD700, 
            0 0 15px #FF4500, 
            0 0 20px #FF69B4, 
            0 0 25px #FF69B4, 
            0 0 35px #FFD700;
    }
    100% {
        text-shadow: 
            0 0 5px #FF69B4, 
            0 0 10px #FFD700, 
            0 0 15px #FF4500, 
            0 0 20px #FFD700, 
            0 0 30px #FF69B4;
    }
}
.vuongmien 
{
  position: absolute;
    display: flex;
    justify-content: center;
    left: 0;
    right: 0;
    bottom: -200px;
    top: 100%;
}

